body {
  font-family: 'Avenir';
  margin: 0;
  margin-bottom: 50px;
  height: 100vh;
}

nav {
  height: 50px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
}

nav a {
  font-size: 16px;
  text-transform: uppercase;
  margin: 0 20px;
}

nav a:hover {
  text-decoration: line-through;
  cursor: pointer;
}

h1 {
  text-align: center;
  font-family: 'Baskerville', serif;
  font-weight: normal;
  font-size: 42px;
}

h2 {
  font-size: 24px;
  text-align: center;
  font-weight: normal;
  margin: 0;
}

h3 {
  font-size: 13px;
  margin-top: 5px;
  margin-bottom: 0;
  font-weight: normal;
}

h5 {
  font-weight: normal;
}

button:hover {
  cursor: pointer;
}

.thumbnail {
  width: 200px;
}

#x {
  font-weight: lighter;
  font-size: 56px;
  text-align: right;
}

header {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: justify;
      justify-content: space-between;
  margin: 0 30px;
}

.container {
  margin: auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: start;
      justify-content: flex-start;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-align: space-between;
      align-items: space-between;
}

.artwork, .artist {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: start;
      justify-content: flex-start;
  margin: 5px 20px;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
.artist {
  width: 20vw;
}

.artist:hover,.artwork:hover {
  cursor: pointer;
}
.artist:hover h3, .artwork:hover h3{
  text-decoration: line-through;
}
.artist img, .artwork img {
  width: 100%;
}

.artwork {
  width: 25vw;
}

#sketch-container {
  position: relative;
  width: 60vw;
  left: 50px;
}

#sketch-container img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: auto;
}



canvas {
  position: absolute;
  left: 0;
  width: 100%;
  z-index: 10;
}

.options {
  float: right;
  margin: 0 20px;
  padding: 20px;
  width: 20vw;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: justify;
      justify-content: space-between;
  font-weight: bold;
  border: 1px solid black;
  height: 300px;
}


.options div, .options button {
  margin: 5px 0;
}

.options div {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

.options label {
  margin-right: 10px;
  margin-bottom: 10px; 
}
/*
#logo {
  font-family: 'Baskerville';
  text-transform: none;
}*/

#logo:after {
  content: 'Defacer'
}

#logo:hover:after {
  content: 'D$F%C#R';
}


.form {
  text-align: center;
}

.form form {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  border: 1px solid black;
  padding: 20px;
  -ms-flex-align: center;
      align-items: center;
  width: 350px;
  margin: 40px auto;
}

.inline-buttons {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.inline-buttons button {
  width: 45%;
  background-color: white;
  color: black;
  border: 1px solid black;
}

input {
  padding: 5px;
  border: 1px solid black;
  font-weight: 500;
}

input[type='color'] {
  background-color: rgba(0,0,0,0);
  border: 0px solid black;
  border-radius: 15px;
  padding: 0;
  -webkit-appearance: none;
  width: 40px;
  height: 25px;
} 

label {
  margin-top: 10px;
  margin-bottom: 5px;
}

button {
  margin: 10px auto;
  padding: 8px;
  width: auto;
  background-color: black;
  color: white;
  border: none;
  font-size: 14px;
  font-family: 'Avenir';
  text-transform: lowercase;
}

.gallery-work {
  width: 20vw;
  position: relative;
  margin: 10px;
  height: 30vw;
}

.gallery-work img {
  position: absolute;
  top: 0;
  width: 100%;
  height: auto;
  left: 0;
}

.gallery-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: start;
      justify-content: flex-start;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  width: 90vw;
  margin: auto;
}

.gallery-work:hover .overlay {
  opacity: 1;
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
}

a {
  color: black;
  text-decoration: none;
}

input[name='title'] {
  font-size: 28px;
  margin: 20px 50px;
  color: black;
}

.artwork-single img{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: auto;  
}

.artwork-single {
  background-color: green;
  margin: 30px auto;
  position: relative;
  left: 0;
  width: 60vw;
}

.single {
  width: 100vw;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.overlay {
  background-color: black;
  color: white;
  text-align: left;
  padding: 15px;
  opacity: 0;
  z-index: 5;
  position: absolute;
  top: 0;
  width: 60%;
  float: center;
}

h4,h5 {
  margin: 0;
}

.back {
  font-size: 14px;
  padding: 2px 8px;
  position: absolute;
  top: 90px;
  left: 30px;
  text-align: left;
  background-color: black; 
}

a.back {
  color: white;
}
a.back:hover {
  text-decoration: line-through;
}


